* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --yellow: #FFD700;
  --red: #E74C3C;
  --green: #27AE60;
  --back: #212121;
  --txt: wheat;
}

body {
  background: var(--back);
  color: var(--txt);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

nav {
  background-color: #333;
  padding: 1rem 0;
  border-bottom: 1px solid var(--yellow);
  margin-bottom: 10px;
}
nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
}
nav ul li {
  margin: 0 15px;
}
nav ul li a {
  color: var(--txt);
  text-decoration: none;
  font-size: 1.1rem;
  padding: 5px 10px;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
}
nav ul li a:hover, nav ul li a.active {
  color: var(--back);
  background-color: var(--yellow);
}